import React, { Component } from 'react'
import '../../../asset/style/index.scss'
export default class Api1 extends Component {
    render() {
        return (
            <div className='bigmain'>
                <h1>React.Component</h1>
                <p>React 的组件可以定义为 class 或函数的形式。class 组件目前提供了更多的功能，这些功能将在此章节中详细介绍。如需定义 class 组件，需要继承 React.Component：</p>
                <pre>
                    <code>
                        {`
                       class Welcome extends React.Component {
                        render() {
                          return <h1>Hello, {this.props.name}</h1>;
                        }
                      }
                       `}
                    </code>
                </pre>
                <p>在 React.Component 的子类中有个必须定义的 render() 函数。</p>
                <p>
                    示例：
                </p>
                <pre>
                    <code>
                        {`
                             class Com extends React.Componet {

                            }
                          
                        `}
                    </code>
                </pre>
                <p>用法介绍</p>
                <pre>
                    <code>
                        {`
                              class HelloMessage extends React.Component {
                                render() {
                                  return (
                                    
                  
                                      Hello {this.props.name}
                                    
                  
                                  );
                                }
                              }
                              
                              ReactDOM.render(
                                <\HelloMessage name="Taylor" />,
                                document.getElementById('hello-example')
                              );
                          
                        `}
                    </code>
                </pre>
            </div>
        )
    }
}