import React, { Component } from 'react';
import { getBaidu } from '../../api/request';
import { debounce } from 'lodash'
import './index.scss';
export default class Task4 extends Component {
    constructor() {
        super();
        this.state = {
            text: '',
            list: [],
            data: [],
            dleList: [],
            backgroundColor: '#953735'
        }
        // 防抖
        this.get = debounce(function (value) {
            getBaidu({ wd: value }).then((data) => {
                console.log(data.s)
                this.setState({
                    list: data.s
                })
            })
        }, 2000)
    }
    // input
    handleChange({ target }) {
        this.setState({
            text: target.value
        })
        this.get(target.value)
    }
    // 提交事件
    submit = () => {
        this.setState({
            data: this.state.list
        })
    }
    // 删除单条数据
    handleClick = (index) => {
        // console.log(index)
        let reItem = this.state.list.splice(index, 1);
        this.setState({
            // list: this.state.list,
            dleList: [...this.state.dleList, reItem[0]]
        })
    }
    // 背景颜色变色
    handleWheel = (event) => {
        // console.log(1)
        var newColor = (parseInt(this.state.backgroundColor.substr(1), 16)
            + event.deltaY * 997).toString(16);
        // console.log(newColor)
        this.setState({
            backgroundColor: '#' + newColor
        })
    }

    render() {
        return <div className='box'>
            <div className='box-header flex flex-bw'>
                <input placeholder='请输入' type='text' value={this.state.text} onChange={(e) => this.handleChange(e)} className='inp' />
                <button className='btn' onClick={this.submit}>提交</button>
            </div>
            <ul style={{ listStyle: 'none', backgroundColor: this.state.backgroundColor }} onWheel={(event) => this.handleWheel(event)}>
                {
                    this.state.data.map((item, index) => {
                        return <li key={index} className='flex flex-bw'>
                            <span>{index + 1}{item}</span>
                            <button className='btn1' onClick={(e) => this.handleClick(index)}>删除</button></li>;
                    })
                }
            </ul>
        </div>
    }
}