import React, { Component } from 'react'

export default class Childl extends Component {
    render() {
        return (
            <div className='conbox1' style={{ background: this.props.background ? 'purple' : 'red' }}>
                <button onClick={this.props.onHandleChange2}>改变主体背景颜色</button>
                <button onClick={this.props.onHandleChange1}>改变自己背景颜色</button>
                <button onClick={this.props.onHandleChange3}>改变右边背景颜色</button>
            </div>
        )
    }
}
