import React from 'react';
import { Icon } from 'antd';
import './index.scss';
import message from '../tool/message.js';
import Login from '../login'
import Register from '../register'
export default class LoginBox extends React.Component {
    constructor() {
        super()
        this.state = {
            userName: '',//名字
            passWord: '',//密码
            email: '',    //邮箱
            NewPassword: '',//重新确认密码
            isShow: true, //登录和注册切换
            ischeck: true,//复选框切换
            num: 0,      //错误次数
            code: '',     //验证码
            isOpen: true  //登录的显示与隐藏
        }
    }

    // input绑定改变事件
    onHandleChanges = ({ target }) => {
        console.log(target.checked)
        this.setState({
            [target.name]: target.name === 'ischeck' ? target.checked : target.value
        })
    }

    // 登录和注册点击获取数据事件
    onSubmitBtn = (pandName = '') => {
        let detail = message(this.state, pandName);
        if (detail.status) {
            alert(detail.msg)
            this.setState({
                num: 0
            })
            if (pandName === 'register') {
                this.state.isShow = true

            }
            if (pandName === 'login') {
                this.setState({
                    isOpen: false
                })
            }

        } else {
            this.setState({
                num: this.state.num + 1
            })
            alert(detail.msg)
        }
        console.log(this.state.num)
    }

    // 切换登录和注册
    onRegister = () => {
        this.setState({
            isShow: !this.state.isShow
        })
    }
    close = () => {
        this.setState({
            isOpen: !this.state.isOpen
        })
    }
    render() {
        let { userName, passWord, email, NewPassword, ischeck, num, code, isOpen } = this.state;
        return (
            <React.Fragment>
                {isOpen && (
                    <div className="zhezhao">
                        <div className='login-box'>
                            {this.state.isShow ? <Login
                                onHandleChanges={this.onHandleChanges}
                                onSubmitBtn={this.onSubmitBtn}
                                onRegister={this.onRegister}
                                num={num}
                                code={code}
                                ischeck={ischeck}
                                userName={userName}
                                passWord={passWord}
                                close={this.close}

                            /> : <Register
                                    onHandleChanges={this.onHandleChanges}
                                    onSubmitBtn={this.onSubmitBtn}
                                    onRegister={this.onRegister}
                                    userName={userName}
                                    email={email}
                                    passWord={passWord}
                                    NewPassword={NewPassword}
                                    close={this.close}
                                />}
                        </div>

                    </div>
                )}
            </React.Fragment>


        )
    }
}