import React from 'react'
import './index.scss'
function Header(props) {
    return <header className='flex flex-bw'>
        <button onClick={() => props.getLink('first')}>1</button>
        <button onClick={() => props.getLink('two')}>2</button>
        <button onClick={() => props.getLink('three')} >3</button>
        <button onClick={() => props.getLink('four')}>4</button>
    </header>
}

export default Header