import React, { Component } from 'react';
import './index.scss'
export default class Header extends Component {
    constructor(props) {
        super()
        console.log(props.showWhd)
      
    }
    render() {
        return (
            <div className='flex flex-bw layui-header'>
                <div className='flex layui-logo'>
                    {/* 左右按钮 */}
                    <div className="layui-logo-toggle" onClick={this.props.onShowWhd}>
                        {
                            this.props.isShow ? <i className="iconfont icon-doubleleft"></i> : <i className="iconfont icon-doubleright"></i>
                        }
                    </div>
                    <div className='layui-logo-brand'>
                        <a href='#'>React后台</a>
                    </div>
                </div>
                <div className='layui-right'>
                    <a href='#' className='child'>
                        <span>
                            <img src='http://m.zhengjinfan.cn/images/0.jpg' className='layui-nav-img' />
                            genuine
                            <i className='iconfont icon-down'></i>
                        </span>
                    </a>
                </div>
            </div>
        )
    }
}
