import React from 'react';
import './index.scss'
import styles from './index.module.scss'
import data from '../data.json'
import { Menu, Icon, Card } from 'antd';
// import { isMoment } from '_moment@2.24.0@moment';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

export default class Task2 extends React.Component {
    constructor() {
        super()
        this.state = {
            user: 'zsy01',
        }
    }
    getLink = (name) => {
        this.setState({
            user: name
        })
    }
    render() {
        var user = this.state.user
        return (<div>
            <h2 style={{ textAlign: "center", margin: "30px " }}>社会人口资料查询系统v1.0</h2>
            <div className='flex '>
                <Menu style={{ width: '256px', background: '#FFF' }} mode="vertical">
                    <SubMenu key="sub1" onTitleClick={() => { this.getLink('zsy01') }} title={<span><Icon type="mail" /><span>海绵宝宝</span></span>}>

                    </SubMenu>
                    <SubMenu key="sub2" onTitleClick={() => { this.getLink('ghx02') }} title={<span><Icon type="appstore" /><span>派大星</span></span>}>

                    </SubMenu>

                </Menu>
                <div>
                    <Card style={{ width: ' 80 %' }}>
                        <img src={data[user].ioc} className={styles.img} />
                        <div className='data'>
                            <p>姓名:{data[user].name}</p>
                            <p>性别:{data[user].sex}</p>
                            <p>年龄:{data[user].age}</p>
                            <p>籍贯:{data[user].birth}</p>
                            <p>就职:{data[user].job}</p>
                        </div>
                        <div>
                            {data[user].oldPass.map((val, index) => {
                                return <p key={index}>{val}</p>
                            })
                            }
                            <p>理想:{data[user].ideal}</p>
                            {/* <p>{data[user].oldPass[0]}</p>
                            <p>{data[user].oldPass[1]}</p> */}
                        </div>
                    </Card>
                </div>
            </div>
        </div>)
    }
}