import React from 'react';
import './index.scss';
import '../../../../asset/style/index.scss'
import { Icon } from 'antd';
export default (props) => {
    const { userName, passWord, email, NewPassword, onHandleChanges, onSubmitBtn, onRegister, close } = props
    {/*注册页面 */ }
    return (
        <div className='denglu'>
            <p>
                注册<span onClick={close} className='close'>X</span>
            </p>
            <form>
                {/* 用户 */}
                <label className='form-label'>
                    <Icon type="user" />
                    <input type='text'  className='login-inp' value={userName} name='userName' onChange={onHandleChanges} placeholder='Username' />
                </label>
                {/* 邮箱 */}
                <label className='form-label'>
                    <Icon type="mail" />
                    <input type='email'  className='login-inp' value={email} name='email' onChange={onHandleChanges} placeholder='email' />
                </label>
                {/* 密码 */}
                <label className='form-label'>
                    <Icon type="lock" />
                    <input type='password'  className='login-inp' value={passWord} name='passWord' onChange={onHandleChanges} placeholder='Password' />
                </label>
                {/* 确认密码 */}
                <label className='form-label'>
                    <Icon type="lock" />
                    <input type='password'  className='login-inp' value={NewPassword} name='NewPassword' onChange={onHandleChanges} placeholder='NewPassword' />
                </label>
                <label className='btn2 flex flex-bw'>
                    <button type='button' onClick={onRegister}>登录</button>
                    <button type='button' onClick={() => { onSubmitBtn('register') }}>注册</button>
                </label>
            </form>
        </div>

    )

}