export default (param = {}, pandName) => {
    const { userName, passWord, email, NewPassword, ischeck, code, num } = param;
    console.log(pandName)
    switch (pandName) {
        case 'register':
            // 验证用户名:只有当不成立的时候才会执行if判断里面的逻辑
            if (userName === '' || userName.length < 2) {
                return {
                    status: false,
                    msg: '您的用户名不许为空,并且长度不小于2位'
                }
            }
            // 验证邮箱:只有当不成立的时候才会执行if判断里面的逻辑
            const reg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/
            if (email === '' || !reg.test(email)) {
                return {
                    status: false,
                    msg: '您的邮箱不许为空，并且不符合邮箱规则'
                }
            }

            // 验证密码:只有当不成立的时候才会执行if判断里面的逻辑
            if (passWord === '' || passWord.length < 6) {
                return {
                    status: false,
                    msg: '您的密码不许为空,并且长度不小于6位'
                }
            }

            // 验证确认密码:只有当不成立的时候才会执行if判断里面的逻辑
            if (passWord !== NewPassword) {
                return {
                    status: false,
                    msg: '您的两次密码不相同，请检查'
                }
            }
        case 'login':
            // 验证用户名:只有当不成立的时候才会执行if判断里面的逻辑
            if (userName === '' || userName.length < 2) {
                return {
                    status: false,
                    msg: '您的用户名不许为空,并且长度不小于2位'
                }
            }
            // 验证密码:只有当不成立的时候才会执行if判断里面的逻辑
            if (passWord === '' || passWord.length < 6) {
                return {
                    status: false,
                    msg: '您的密码不许为空,并且长度不小于6位'
                }
            }

            // 验证复选框:只有当不成立的时候才会执行if判断里面的逻辑
            if (ischeck === false) {
                return {
                    status: false,
                    msg: '请同意协议'
                }
            }
            // 判断验证码：只有当不成立的时候才会执行if判断里面的逻辑


            if (num >= 2) {
                if (code === '' || code.length < 4) {
                    return {
                        status: false,
                        msg: '您的验证码不许为空,并且长度不小于4位'
                    }
                }
            }
        default:
            return {
                status: true,
                msg: `恭喜，${pandName === 'login' ? '登录' : '注册'}成功！`
            }

    }

}