import React, { Component } from 'react';


import './index.scss';



import SiderDemo from '../sides'
// import Content from '../content'
import Shouye from '../shouye/index.jsx'
import Static from '../Zujian/static';
import Class from '../Zujian/class';
import Hoc from '../Zujian/hoc'
import XuanRan from '../Zujian/xuanran'
import Api1 from '../Api/Api1';
import State from '../Api/state';
import Task1 from '../Task/7-13/components/html/index.jsx'
import Task2 from '../Task/7-10/components/home/index.jsx'
import Task3 from '../Task/7-9/components/home/index.jsx'
import Task4 from '../Task/7-8/components/home/index.jsx'
import Task5 from '../Task/7-15/components/html/index.jsx'

import './index.scss'

export default class Main extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ModuleName: 'first'
        }
        console.log(this.props)
    }
    // 控制一级导航的点击事件
    onchangeClick = (name) => {
        console.log(name)
        this.setState({
            ModuleName: name
        })
    }
    ComModulesde() {
        let ComModule = ''
        switch (this.state.ModuleName) {
            case 'first': ComModule = <Shouye></Shouye>
                break;
            case 'static': ComModule = <Static></Static>
                break;
            case 'class': ComModule = <Class></Class>
                break;
            case 'hoc': ComModule = <Hoc></Hoc>
                break;
            case 'xuanran': ComModule = <XuanRan></XuanRan>
                break;
            case 'api1': ComModule = <Api1></Api1>
                break;
            case 'state': ComModule = <State></State>
                break;
            case 'task1': ComModule = <Task1></Task1>
                break;
            case 'task2': ComModule = <Task2></Task2>
                break;
            case 'task3': ComModule = <Task3></Task3>
                break;
            case 'task4': ComModule = <Task4></Task4>
                break;
            case 'task5': ComModule = <Task5></Task5>
                break;

        }
        return ComModule
    }
    render() {
        let ComModule = this.ComModulesde()

        return (
            <div className=" flex bag" >
                {/* 左边 */}
                <div className={this.props.isShow ? 'nav' : 'hide'}>
                    <SiderDemo onchangeClick={this.onchangeClick} className={this.props.isShow ? 'nav' : 'hide'}></SiderDemo>
                </div>
                {/* 右边 */}
                <div className={this.props.isShow ? 'main big' : 'show big'}>
                    {
                        ComModule
                    }
                </div>
            </div>
        )


    }
}