import React from 'react';
import Header from '../header';
import First from '../main/first';
import Two from '../main/two';
import Three from '../main/three';
import Four from '../main/four'
import styles from './index.Modules.scss';

export default class Task3 extends React.Component {
    constructor() {
        super();
        this.state = {
            Dom: '',
            isShow: false,
            isShows: false
        }
    }
    // 接受子组件传回来的值
    getLink = (link) => {
        this.setState({
            Dom: link

        })
    }
    render() {
        let Dom = '';
        switch (this.state.Dom) {
            case 'first':
                Dom = <First></First>
                break;
            case 'two':
                Dom = <Two></Two>
                break;
            case 'three':
                Dom = <Three></Three>
                break;
            case 'fone':
                Dom = <Four></Four>
                break;
            default:
                Dom = <First />
        }
        return (
            <div className='box'>
                <Header getLink={this.getLink} />

                <div className='cen'>
                    {/* {Dom} */}
                    <div className='main-top flex  flex-bw'>
                        <span> {Dom}</span>
                        <div>
                            <button onClick={() => { this.setState({ isShow: !this.state.isShow }) }}>组件1</button>
                            <button onClick={() => { this.setState({ isShows: !this.state.isShows }) }}>组件2</button>
                        </div>

                    </div>
                    <div className='main-buttom flex  flex-bw'>
                        {/* <Child /> */}
                        {this.state.isShow && <div>子组件1根据父组件传入的值来控制是否显示</div>}
                        {this.state.isShows && <div>子组件2根据父组件传入的值来控制是否显示</div>}
                    </div>
                </div>
            </div>
        )
    }
}