import React, { Component } from 'react'
import Child1 from '../child1'
import Child2 from '../child2'
import './index.scss'
export default class Task5 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            color: true,
            main: true,
            right: true
        }
    }
    // 改变主体颜色事件
    onHandleChange2 = ({ target }) => {
        this.setState({
            main: !this.state.main
        })
    }
    // 改变左边颜色事件
    onHandleChange1 = ({ target }) => {
        this.setState({
            color: !this.state.color
        })
    }
    // 改变右边颜色事件
    onHandleChange3 = ({ target }) => {
        this.setState({
            right: !this.state.right
        })
    }
    render() {
        const { color, main, right } = this.state;
        return (
            <div className='main-box' style={{ background: main ? 'blue' : 'pink' }}>
                <div className='flex flex-bw headbox'>
                    <button onClick={this.onHandleChange1}>改变左边背景色</button>
                    <button onClick={this.onHandleChange3}>改变右边背景色</button>
                </div>
                <div className='flex flex-bw conbox'>
                    <Child1 background={color} onHandleChange1={this.onHandleChange1} onHandleChange2={this.onHandleChange2} onHandleChange3={this.onHandleChange3} />
                    <Child2 onHandleChange3={this.onHandleChange3} right={right} />
                </div>
            </div>
        )
    }
}
