import React from 'react';
import { Icon } from 'antd';
import './index.scss';
export default (props) => {
    const { userName, passWord, onHandleChanges, onSubmitBtn, onRegister, ischeck, num, code, close } = props
    {/* 登录页面 */ }
    return (
        <div className='denglu'>
            <p>
                登录<span onClick={close} className='close'>X</span>
            </p>
            <form>
                {/* 用户 */}
                <label className='form-label'>
                    <Icon type="user" />
                    <input type='text' className='login-inp' name='userName' value={userName} placeholder="Username" onChange={onHandleChanges} />
                </label>
                {/* 密码 */}
                <label className='form-label'>
                    <Icon type="lock" />
                    <input type='password' className='login-inp' name='passWord' value={passWord} placeholder="Password" onChange={onHandleChanges} />
                </label>
                {
                    num >= 2 &&
                    <label className='code'>
                        验证码： <input type="text" className='login-inp' name='code' value={code} onChange={onHandleChanges} />
                    </label>

                }

                {/* 复选框 */}
                <label className='label3'>
                    <input type='checkbox' name='ischeck' checked={ischeck}
                        onChange={onHandleChanges} />Remember me
                <input type='button' className='btn' value='注册' onClick={onRegister} />
                </label>
                {/* 登录 */}
                <label className=' login'>
                    <input type='button' className='login-btn' value=' Log in' onClick={() => { onSubmitBtn('login') }} />
                </label>
            </form>
        </div>

    )

}