import React, { Component } from 'react';
import Header from '../header/index';
import Main from '../main'
import LoginBox from '../../7-11task/components/loginBox/index.jsx'
import './index.scss';
export default class Home extends Component {
    constructor() {
        super()
        this.state = {
            isShow: true,
        }
    }
    // 控制动画效果
    onShowWhd = () => {
        this.setState({
            isShow: !this.state.isShow
        })
    }

    // render方法
    render() {
        return <div className='layui-layout-admin'>
            {/* 登录盒子 */}
            <LoginBox />
            {/* header部分 */}
            <Header isShow={this.state.isShow} onShowWhd={this.onShowWhd} />
            {/* 主体部分 */}
            <Main isShow={this.state.isShow}></Main>
        </div>
    }
}
